.blog{

    padding: 80px 0 100px;
    background: #F3F5F4;
    

    &__descr{
        text-align: center;
    }

    &__title{
        text-align: center;
        margin-bottom: 40px;
    }

    &__list{
        display: flex;
        flex-wrap: wrap;
        // grid-template-columns: repeat(2,580px);
        gap: 24px 20px;
        
    }

    .swiper2{
        overflow: hidden;
    }

    &-link{
        padding: 16px 36px;
        display: block;
        max-width: 221px;
        margin: 50px auto 0;
        font-size: 16px;
        font-weight: 600;
        line-height: 20px;
        letter-spacing: 0.192px;
        color: #FDFDFD;
        border-radius: 4px;
        background: #1A3E3E;
    }

    &__item{
        display: flex;
        max-width: 580px;
        gap: 0 24px;
        padding: 16px;
        border-radius: 10px;
        background: #FDFDFD;

        &-img{
            max-width: 100%;
            border-radius: 6px;
        }

        &-title{
            font-size: 24px;
            color: #1A3E3E;
            margin-bottom: 24px;
            font-weight: 700;
            line-height: 32px;

        }

        &-text{
            color: #18121E;
            font-size: 14px;
            font-weight: 400;
            line-height: 20px;
            overflow: hidden;
            
            
        }

        &-block{
            margin: 16px 0 16px 0;
            display: flex;
            flex-direction: column;
        }
        &-bottom{
            margin-top: auto;
            display: flex;
            justify-content: space-between;
        }
        &-date{
            color: #F2BE22;
            font-size: 14px;
            font-weight: 700;
            line-height: 20px;
        }
        &-link{
            font-size: 14px;
            font-weight: 700;
            line-height: 20px;
            color: #1A3E3E;
        }

    }
}


@media (max-width:1218px){
    .blog{

        padding: 50px 0;

        &__descr{
            text-align: left;
        }

        &__title{
            text-align: left;
        }

        &__list{
            
        }

        &__item{
            margin: 0 auto;
        }
    }
}

@media (max-width:769px){
    .blog{

        // .swiper-slide{
        //     width: 285px;
        // }

        

        &__list{
            gap: 0;
            flex-wrap: nowrap;
            
        }

        &__item{
            display: block;
            padding: 0;
            width: 285px;
            height: 500px;

            &-title{
                font-size: 20px;
                margin-bottom: 16px;
            }

            &-text{
                margin-bottom: 16px;
                height: 80px;
                overflow: hidden;
            }

            &-block{
                padding: 0 16px;
            }

            &-img{
                width: 100%;
                object-fit: cover;
                height: 280px;
            }
        }
    }
}